.country-card {
  position: relative;

  &::before {
    position: absolute;
    content: "";
    top: 88px;
    left: 25px;
    z-index: 2;

    width: 35px;
    height: 24px;

    background-size: 35px 24px;
    background-repeat: no-repeat;
  }

  &--thailand::before {
    background-image: url("../img/flag-thailand.svg");
  }

  &--france::before {
    background-image: url("../img/flag-france.svg");
  }

  &--czech::before {
    background-image: url("../img/flag-czech.svg");
  }
}

.country-card__picture {
  display: block;
  line-height: 0;
}

.country-card__img {
  border-radius: 10px 10px 0 0;
}

.country-card__wrapper {
  position: relative;
  z-index: 1;

  min-height: 90px;
  margin-top: -11px;
  padding-top: 27px;
  padding-bottom: 11px;
  padding-left: 25px;
  padding-right: 75px;

  background-color: #ffffff;
  border-radius: 10px;
}

.country-card__heading {
  margin: 0;

  font-size: 21px;
  line-height: 21px;
  font-weight: 700;
  color: #161c35;
}

.country-card__slogan {
  font-size: 16px;
  line-height: 18px;
  font-weight: 400;
  color: rgba(#444444, 0.3);
  letter-spacing: 0.35px;
}

.country-card__counter {
  position: absolute;
  top: 24px;
  right: 25px;

  padding-left: 19px;

  font-size: 16px;
  line-height: 30px;
  font-weight: 400;
  color: rgba(#444444, 0.3);

  &::before {
    position: absolute;
    content: "";
    top: 8px;
    left: 0;

    width: 11px;
    height: 11px;

    background-image: url("../img/user.svg");
    background-size: 11px;
    background-repeat: no-repeat;
    opacity: 0.1;
  }
}

@media (min-width: $tablet-width) {
  .country-card {
    display: flex;

    &::before {
      top: 15px;
      left: auto;
      right: 151px;

      width: 70px;
      height: 47px;

      background-size: 70px 47px;
    }
  }

  .country-card__picture {
    order: 1;
  }

  .country-card__img {
    width: 187px;
    height: 140px;

    border-radius: 0 20px 20px 0;
  }

  .country-card__wrapper {
    display: flex;
    flex-direction: column;
    flex-grow: 1;
    margin-top: 0;
    padding-top: 30px;
    padding-bottom: 50px;
    padding-left: 30px;

    border-radius: 20px 0 0 20px;
  }

  .country-card__heading {
    font-size: 24px;
    line-height: 24px;
  }

  .country-card__slogan {
    font-size: 20px;
    line-height: 20px * 1.4; //      
  }

  .country-card__counter {
    top: auto;
    bottom: 16px;
    left: 30px;
    right: auto;

    padding-left: 31px;

    font-size: 20px;
    line-height: 30px;

    &::before {
      top: 2px;

      width: 20px;
      height: 20px;

      background-size: 20px;
    }
  }
}

@media (min-width: $desktop-width) {
  .country-card::before {
    top: 23px;
  }

  .country-card__wrapper {
    padding-top: 24px;
    padding-left: 29px;
  }
}
